<style lang=less>
  @import '../../../assets/index.less';
  @import './lowerIdentity.less';
</style>
<template>
  <div class="base-info lower-identity">
    <!-- 初级认证 -->
    <p class="title">
      <span></span>{{$t('center.lowerIdentity[0]')}}
    </p>
    <el-form  class="lower-identity-form" :model="form">
      <el-form-item :label="$t('center.lowerIdentity[1]')">
        <el-input v-model="form.name" :placeholder="$t('center.lowerIdentity[2]')"></el-input>
      </el-form-item>
      <el-form-item :label="$t('center.lowerIdentity[7]')">
        <el-input v-model="form.card" :placeholder="$t('center.lowerIdentity[3]')"></el-input>
      </el-form-item>
      <el-button class="submit" type="primary" :loading="submitLoading" @click="submit">{{$t('center.lowerIdentity[4]')}}</el-button>
    </el-form>
  </div>
</template>
<script>
import {setSm} from "@/api/user.js"
export default {
  data () {
    return {
      submitLoading:false,
      form:{//表单中数据
        name:'',
        card:''
      }
    }
  },
  created () {
  },
  watch: {
  },
  methods: {
    submit(){//提交

      if(!this.form.name){
        this.$public.msg(this.$t('center.lowerIdentity[5]'),'warning',this)
        return
      }

      if(!this.$public.isCardID(this.form.card)){
        this.$public.msg(this.$t('center.lowerIdentity[6]'),'warning',this)
        return
      }
      this.submitLoading = true
      let _data = {
        realname: this.form.name,
        card_num: this.form.card,
        type: 1
      }
      setSm(_data).then((res)=>{
        this.submitLoading = false
        if(res.data.status == 200){
          this.$public.msg(res.data.msg,'success',this)
          this.$router.back()
        } else{
          this.$public.msg(res.data.msg,'error',this)
          this.form.oldPwd = ''
          this.form.pwd = ''
          this.form.repeatPwd = ''
        }
      })

    }
  }
}
</script>



